package com.gsb.vyapar.bhagya.presentation.auth.util

import com.gsb.vyapar.bhagya.datasource.mqtt.MQTTManager
import com.gsb.vyapar.bhagya.domain.failureReason
import com.gsb.vyapar.bhagya.domain.getEmailMessage
import com.gsb.vyapar.bhagya.domain.getMobileMessage
import com.gsb.vyapar.bhagya.domain.isFailureMessage
import com.gsb.vyapar.bhagya.domain.isSuccessMessage
import com.gsb.vyapar.bhagya.domain.toMessageString
import com.gsb.vyapar.bhagya.interactor.manager.StateManager.launchDefault
import com.gsb.vyapar.bhagya.interactor.util.hideLoading
import com.gsb.vyapar.bhagya.interactor.util.showError
import com.gsb.vyapar.bhagya.interactor.util.showLoading
import com.gsb.vyapar.bhagya.interactor.util.showSuccess
import com.gsb.vyapar.bhagya.presentation.Screens
import com.gsb.vyapar.bhagya.presentation.auth.uistate.getAuthUiState
import com.gsb.vyapar.bhagya.presentation.navigation.util.pushView
import kotlinx.coroutines.delay
import kotlinx.datetime.Clock

fun requestOtpMobile(to: String, message: String, status: (Result<Unit>) -> Unit) {
    showLoading()
    launchDefault {
        MQTTManager.connect()
        var timoutCount = 0
        var isSuccess: Boolean
        var reason = ""
        while(!MQTTManager.connected && timoutCount <= 5) {
            delay(1000)
            timoutCount += 1
        }
        isSuccess = MQTTManager.connected
        if (isSuccess) {
            val requestId = Clock.System.now().epochSeconds.toString()
            MQTTManager.subscribe(requestId)
            delay(1000)
            MQTTManager.publish("PGsbVyaparBhagya", getMobileMessage(requestId, to, message).toMessageString())
            timoutCount = 0
            while (MQTTManager.messages.size <= 0 && timoutCount <= 5) {
                delay(1000)
                timoutCount += 1
            }
            val messages = MQTTManager.messages
            isSuccess = false
            messages.forEach {
                if (it.isSuccessMessage(requestId)) {
                    isSuccess = true
                } else if (it.isFailureMessage(requestId)) {
                    isSuccess = false
                    reason = it.failureReason(requestId).orEmpty()
                }
            }
        }
        hideLoading()
        if (!isSuccess && reason.isEmpty()) {
            reason = "Time Out, Please try again"
        }
        if (isSuccess) {
            showSuccess("OTP successfully sent to $to")
            status(Result.success(Unit))
        } else {
            showError(reason)
            status(Result.failure(Throwable(reason)))
        }
        MQTTManager.disconnect()
    }
}

