fun Int.formatSecondsToHHMM(): String {
    val minutes = this / 60
    val remainingSeconds = this % 60
    return minutes.toString().padStart(2, '0') + ":" + remainingSeconds.toString().padStart(2, '0')
}

fun launchLoginMobile() {
    getAuthUiState().resetAuthUiState(false)
    pushView(Screens.Login)
}

fun launchVerifyEmail() {
    getAuthUiState().resetAuthUiState(true)
    pushView(Screens.Login)
}