package com.gsb.vyapar.bhagya.util

val emailRegex = "^[A-Za-z0-9+_.-]+@[A-Za-z0-9.-]+\\.[A-Za-z]+\$"
fun isValidEmail(email: String): Boolean {
    return email.matches(emailRegex.toRegex())
}

fun isEmailError(text: String): Boolean {
    return text.isNotEmpty() && !isValidEmail(text)
}

val passwordRegex = "^(?=.*[A-Z])(?=.*[a-z])(?=.*\\W).{8,}\$"
fun isValidPassword(password: String): Boolean {
    return password.matches(passwordRegex.toRegex())
}

fun isPasswordError(text: String): Boolean {
    return text.isNotEmpty() && !isValidPassword(text)
}


fun isValidFirstName(firstName: String): Boolean {
    return firstName.length > 2
}

fun isFirstNameError(text: String): Boolean {
    return text.isNotEmpty() && !isValidFirstName(text)
}

fun isValidLastName(lastName: String): Boolean {
    return lastName.length > 2
}

fun isLastNameError(text: String): Boolean {
    return text.isNotEmpty() && !isValidLastName(text)
}

val mobileRegex = "^[6789]\\d{9}$"

fun isValidMobileNumber(mobileNumber: String): Boolean {
    return mobileNumber.length == 10 && mobileNumber.toDoubleOrNull() != null && mobileNumber.matches(mobileRegex.toRegex()) && !isSequentialPatterns(mobileNumber) && !(mobileNumber.all { it == mobileNumber[0] })
}

fun isMobileError(text: String): Boolean {
    return text.isNotEmpty() && !isValidMobileNumber(text)
}

fun isSequentialPatterns(mobileNumber: String): Boolean {
    val sequentialPatterns = listOf("1234567890", "9876543210")
    return sequentialPatterns.contains(mobileNumber)
}

fun isValidOtp(otp: String): Boolean {
    return otp.length == 6 && otp.toIntOrNull() != null
}

fun isOtpError(text: String): Boolean {
    return text.isNotEmpty() && !isValidOtp(text)
}

val gstRegex = "^[0-9]{2}[A-Z]{5}[0-9]{4}[A-Z]{1}[1-9A-Z]{1}Z[0-9A-Z]{1}$"
fun isValidGst(gst: String): Boolean {
    return gst.matches(gstRegex.toRegex())
}

fun isGstError(text: String): Boolean {
    return text.isNotEmpty() && !isValidGst(text)
}

val adhaarRegex = "^\\d{12}$"
val panRegex = "^[A-Z]{5}[0-9]{4}[A-Z]$"
fun isValidAdhaarOrPan(aadharPan: String): Boolean {
    return aadharPan.matches(adhaarRegex.toRegex()) || aadharPan.matches(panRegex.toRegex())
}

fun isAdhaarOrPanError(text: String): Boolean {
    return text.isNotEmpty() && !isValidAdhaarOrPan(text)
}

fun isValidZipcode(zipcode: String): Boolean {
    return zipcode.length == 6
}

fun isZipcodeError(text: String): Boolean {
