import org.jetbrains.compose.resources.painterResource

@Composable
fun LoginView(uiState: UiState, onTriggerEvent: (UiEvents) -> Unit) {
    var email by remember { mutableStateOf("") }
    var mobile by remember { mutableStateOf("") }
    var otp by remember { mutableStateOf("") }
    var isValidOtp by remember { mutableStateOf(false) }
    Surface(
        modifier = Modifier.fillMaxSize(),
        color = MaterialTheme.colors.background
    ) {
        Column(
            modifier = Modifier.fillMaxWidth()
        ) {
            Toolbar(if (uiState.authUiState.isEmail) "Verify Email" else "Login")
            Column(
                modifier = Modifier.verticalScroll(rememberScrollState()).fillMaxWidth().padding(16.dp),
                horizontalAlignment = Alignment.CenterHorizontally
            ) {
                Spacer(Modifier.height(15.dp))
                CommonImage(painterResource(splash), modifier = Modifier.width(200.dp).height(200.dp))
                Spacer(Modifier.height(15.dp))
                if (uiState.authUiState.isEmail) {
                    EmailEditText(email) { email = it }
                } else {
                    MobileNumberEditText(mobile) { mobile = it }
                }
                if (uiState.authUiState.isOtpSent) {
                    Spacer(Modifier.height(8.dp))
                    OtpEditText(otp) {
                        otp = it
                        isValidOtp = isValidOtp(otp)
                    }
                    if (isValidOtp) {
                        Spacer(Modifier.height(16.dp))
                        GVBButton(text = "Verify OTP") {}
                    }
                    if (uiState.authUiState.remainingTime <= 0) {
                        Spacer(Modifier.height(40.dp))
                        Row(Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.Center) {
                            Text14Regular("Did not receive an OTP?", message)
                            Spacer(Modifier.width(5.dp))
                            Text14RegularBB("Resend OTP", black, Modifier.clickable {  })
                        }
                        Spacer(Modifier.height(36.dp))
                    } else {
                        Spacer(Modifier.height(40.dp))
                        Row(Modifier.fillMaxWidth(), horizontalArrangement = Arrangement.Center) {
                            Text14Regular("Resend OTP in ", message)
                            Spacer(Modifier.width(5.dp))
                            Text14RegularBB(uiState.authUiState.getRemainingTimeString(), black)
                        }
                        Spacer(Modifier.height(36.dp))
                    }
                } else {
                    Spacer(Modifier.height(16.dp))
                    GVBButton(text = "Send OTP") {
                        onTriggerEvent(UiEvents.AuthEvents(if (uiState.authUiState.isEmail) AuthUiEvents.SendEmailOtp(email) else AuthUiEvents.SendMobileOtp(mobile)))
                    }
                }
            }
        }
    }
}