package com.gsb.vyapar.bhagya.interactor.uistate

import androidx.compose.runtime.mutableStateMapOf
import androidx.compose.runtime.snapshots.SnapshotStateMap
import androidx.compose.ui.graphics.ImageBitmap
import com.gsb.vyapar.bhagya.datasource.preference.Preference
import com.gsb.vyapar.bhagya.domain.Categories
import com.gsb.vyapar.bhagya.domain.Config
import com.gsb.vyapar.bhagya.domain.Info
import com.gsb.vyapar.bhagya.domain.SubCategories
import com.gsb.vyapar.bhagya.domain.decodeCategoriesString
import com.gsb.vyapar.bhagya.domain.decodeConfigString
import com.gsb.vyapar.bhagya.domain.decodeInfoString
import com.gsb.vyapar.bhagya.domain.decodeSubCategoriesString
import com.gsb.vyapar.bhagya.interactor.util.checkImageCache
import com.gsb.vyapar.bhagya.presentation.Screens
import com.gsb.vyapar.bhagya.presentation.Screens.*
import com.gsb.vyapar.bhagya.presentation.auth.uistate.AuthUiState
import com.gsb.vyapar.bhagya.presentation.bottomsheet.BottomSheets
import com.gsb.vyapar.bhagya.presentation.category.uistate.CategoriesViewUiState
import com.gsb.vyapar.bhagya.presentation.dialog.uistate.DialogUiState
import com.gsb.vyapar.bhagya.toComposeImageBitmap
import com.gsb.vyapar.bhagya.util.Stack

data class UiState(
    val isPortrait: Boolean = true,
    val mainView: Screens = Splash,
    val navView: Screens? = null,
    val navViewStack: Stack<Screens> = Stack(),
    val loadingUiState: DialogUiState? = null,
    val config: String = Preference.config,
    val categories: String = Preference.categories,
    val subCategories: String = Preference.subCategories,
    val info: String = Preference.info,
    val imageMap: SnapshotStateMap<String, ByteArray> = mutableStateMapOf(),
    val bottomSheetView: BottomSheets? = null,
    val categoriesViewUiState: CategoriesViewUiState = CategoriesViewUiState(),
    val errorUiState: DialogUiState? = null,
    val successUiState: DialogUiState? = null,
    val authUiState: AuthUiState = AuthUiState(),
) {
    fun getConfig(): Config {
        return config.decodeConfigString()
    }

    fun getCategories(): Categories {
        return categories.decodeCategoriesString()
    }

    fun getSubCategories(): SubCategories {
        return subCategories.decodeSubCategoriesString()
    }

    fun getInfo(): Info {
        return info.decodeInfoString()
    }

    fun getImage(url: String): ImageBitmap? {
        checkImageCache(url)
        return imageMap[url]?.toComposeImageBitmap()
    }
}
